import React, {Component} from 'react';
import { Grid, Cell } from 'react-mdl';
import "./test.css";


class test extends Component{

    constructor(props){
        super(props);

        this.state = {
            username1:'',
            username2:'',
            items1: [],
            items2: []
        };

        this.inputUpdated1 = this.inputUpdated1.bind(this);
        this.inputUpdated2 = this.inputUpdated2.bind(this);

    }

    //This is terrible coding but I'm gonna keep it like this just to test
    inputUpdated1(e){
        const {value} = e.target;
        this.setState({username1:value});
    }

    inputUpdated2(e){
        const {value} = e.target;
        this.setState({username2:value});
    }

    onSubmit(username){
        this.setState({username});
    }

    render() {
        return(
            <div style={{width: '100%', margin: 'auto'}}>
                {/*This is how you comment. Has to be in a div*/}
                <Grid className='about-grid'>
                    <Cell col={12}>
                        <div className = 'about-text'>
                            <h1>I'm testing here. Don't come in</h1> 
                         
                        </div>

                    </Cell>
                    <Cell col={5}>
                        <div className = 'about-paragraph'>
                            <form>
                                <label htmlFor='playername'>Player 1 Name</label>
                                <input className = 'form-control'
                                type='input'
                                name='pname'
                                value={this.state.username}
                                onInput={this.inputUpdated1}/>
                                <button type="submit" className='button-submit'>SUBMIT</button>
                            </form>
                            <p>
                            </p>
                        </div>
                    </Cell>

                    <Cell col={2}>
                        <div className = 'about-paragraph'>
                            <form>
                            </form>
                            <p>
                            </p>
                        </div>
                    </Cell>

                    <Cell col={5}>
                        <div className = 'about-paragraph'>
                            <form>
                                <label htmlFor='playername'>Player 2 Name</label>
                                <input className = 'form-control'
                                type='input'
                                name='pname'
                                onInput={this.inputUpdated2}/>

                                <button type="submit" className='button-submit'>SUBMIT</button>
                            </form>
                            <p>
                            </p>
                        </div>
                    </Cell>
                </Grid>

            </div>    

        )
    }
}

export default test;

